// BASIC
import React, { Component } from 'react'
import styled, {css, keyframes} from 'styled-components'
import {Link} from 'react-router-dom'
// ICONS
import {MdExitToApp} from 'react-icons/md'
import {FaGamepad} from 'react-icons/fa'
import {GiTwoCoins} from 'react-icons/gi'
import {TiChartLine} from 'react-icons/ti'
// ICONS
import cat1 from '../../../Images/icons/premium/cat-pack/cat1.svg'
import cat2 from '../../../Images/icons/premium/cat-pack/cat2.svg'
import cool1 from '../../../Images/icons/premium/cool-pack/cool1.svg'
import cool2 from '../../../Images/icons/premium/cool-pack/cool2.svg'
import crazy1 from '../../../Images/icons/premium/crazy-pack/crazy1.svg'
import crazy2 from '../../../Images/icons/premium/crazy-pack/crazy2.svg'
import devil1 from '../../../Images/icons/premium/devil-pack/devil1.svg'
import devil2 from '../../../Images/icons/premium/devil-pack/devil2.svg'
import nerd1 from '../../../Images/icons/premium/nerd-pack/nerd1.svg'
import nerd2 from '../../../Images/icons/premium/nerd-pack/nerd2.svg'
import nice1 from '../../../Images/icons/premium/nice-pack/nice1.svg'
import nice2 from '../../../Images/icons/premium/nice-pack/nice2.svg'

const fade = keyframes`
	from {
		opacity: 0;
		transform: scale(0.9);
	}
	to {
		opacity: 1;
		transform: scale(1);
	}
`
const ShopComponent = styled.div`
	display: none;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	flex-direction: column;
	padding: 0 50px;
	align-items: center;
	background-color: var(--color-dark);
	z-index: 5;
	font-size: 20px;
	color: var(--color-primary);
	animation: ${fade} 0.3s ease;
	${props =>
		props.show &&
		css`
			display: flex;
		`
	};
`
const ShopTitle = styled.h1`
	font-size: 30px;
	margin: 20px 0;
`
const Info = styled.div`
	display: flex;
	width: 100%;
	justify-content: space-between;
	margin-bottom: 50px;
`
const Coins = styled.div``
const Text = styled.span`
display: none;
	@media (min-width: 500px) {
		display: inline;
	}
`
const CoinIcon = styled(GiTwoCoins)`
	transform: translateY(3px);
`
const Experience = styled.div``
const ExperienceIcon = styled(TiChartLine)`
	transform: translateY(3px);
`
const Number = styled.strong`
	color: var(--color-decorative);
`
const Products = styled.div`
	width: 90vw;
	max-width: 1000px;
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
	height: calc(100vh - 250px);
	overflow-y: scroll;
	/* background-color: gray; */
	::-webkit-scrollbar {
		width: 0;
	}
`
const animation1 = keyframes`
	0% {
		opacity: 0;
		transform: translate(-50%, -50%) rotate(-5deg) scale(0.7);
	}
	10% {
		opacity: 1;
		transform: translate(-50%, -50%) rotate(0) scale(1.2);
	}
	25% {
		opacity: 1;
		transform: translate(-50%, -50%) rotate(0) scale(1);
	}
	40% {
		opacity: 1;
		transform: translate(-50%, -50%) rotate(0) scale(1.1);
	}
	50% {
		opacity: 0;
		transform: translate(-50%, -50%) rotate(5deg) scale(0.7);
	}
	100% {
		opacity: 0;
	}
`
const animation2 = keyframes`
	0% {
		opacity: 0;
	}
	50% {
		opacity: 0;
		transform: translate(-50%, -50%) rotate(-5deg) scale(0.7);
	}
	60% {
		opacity: 1;
		transform: translate(-50%, -50%) rotate(0) scale(1.2);
	}
	75% {
		opacity: 1;
		transform: translate(-50%, -50%) rotate(0) scale(1);
	}
	90% {
		opacity: 1;
		transform: translate(-50%, -50%) rotate(0) scale(1.1);
	}
	100% {
		opacity: 0;
		transform: translate(-50%, -50%) rotate(5deg) scale(0.7);
	}
`
const ProductItem = styled.img`
	position: absolute;
	top: 50%;
	left: 50%;
	width: 80%;
	height: 80%;
	display: block;
	transform: translate(-50%, -50%);
	:first-child {
		opacity: 0;
	}
	${props =>
		props.first &&
		css`
			animation: ${animation1} 2s infinite both;
		`
	};
	${props =>
		props.second &&
		css`
			animation: ${animation2} 2s infinite both;
		`
	};
	/* animation-play-state: paused; */
`
const Product = styled.div`
	position: relative;
	min-width: 150px;
	height: 150px;
	margin: 10px;
	border: 2px solid var(--color-secondary);
	border-radius: 20px;
	background-color: var(--color-bg);
	/* :hover ${ProductItem} {
		animation-play-state: running;
	} */
`
const Exit = styled.div`
	position: absolute;
	bottom: 30px;
	width: 100%;
	display: flex;
	justify-content: space-evenly;
`
const Icon = styled.div`
	font-size: 40px;
	color: var(--color-decorative);
	transition: 0.3s ease;
	:hover {
		cursor: pointer;
		opacity: 0.5;
		transform: scale(0.99);
	}
`

class Shop extends Component {
	state = {
		catAnimation: false,
		coolAnimation: false,
		crazyAnimation: false,
		devilAnimation: false,
	}
	play = (e) => {
		// [...e.currentTarget.children].forEach((element) => {
		// 	console.log(element);
		// 	element.style.animationPlayState = "running";
		// })
		Object.keys(this.state).forEach(name => this.setState({[name]: false}))
		!this.state[`${e.currentTarget.id}Animation`] ? this.setState({[`${e.currentTarget.id}Animation`]: true}) : this.setState({[`${e.currentTarget.id}Animation`]: false})
		// this.setState({coolAnimation: true})
		console.log(e.currentTarget.id);
	}
	render() {
		return (
			<ShopComponent show={this.props.show}>
				<ShopTitle>EasyShop</ShopTitle>
				<Info>
					<Coins>
						<Text>EasyCoins:</Text> <Number>{this.props.coins} <CoinIcon /></Number>
					</Coins>
					<Experience>
						<Text>Dowiadczenie:</Text> <Number>{this.props.experience} <ExperienceIcon /></Number>
					</Experience>
				</Info>
				<Products>
					<Product id="cat" onClick={this.play}>
						<ProductItem hide first={this.state.catAnimation} src={cat1} />
						<ProductItem second={this.state.catAnimation} src={cat2} />
					</Product>
					<Product id="cool" onClick={this.play}>
						<ProductItem hide first={this.state.coolAnimation} src={cool1} />
						<ProductItem second={this.state.coolAnimation} src={cool2} />
					</Product>
					<Product id="crazy" onClick={this.play}>
						<ProductItem hide first={this.state.crazyAnimation} src={crazy1} />
						<ProductItem second={this.state.crazyAnimation} src={crazy2} />
					</Product>
					<Product id="devil" onClick={this.play}>
						<ProductItem hide first={this.state.devilAnimation} src={devil1} />
						<ProductItem second={this.state.devilAnimation} src={devil2} />
					</Product>
					
				</Products>
				<Exit>
					<Link to='/'>
						<Icon>
							<FaGamepad />
						</Icon>
					</Link>
					<Icon>
						<MdExitToApp onClick={this.props.onBack} />
					</Icon>
				</Exit>
			</ShopComponent>
		);
	}
}

export default Shop